.title {
   margin-right: 2rem;
}
.emailListContainer {
   width: 50%;
   border: 1px solid #ced4da;
   display: flex;
   flex-wrap: wrap;
   justify-content: flex-end;

   padding: 1rem 0;
   margin-right: 1rem;
}
.emailItem {
   background-color: #ced4da;
   border-radius: 1rem;
   padding: 0.3rem 0.5rem;
   margin: 0.3rem;
}
.deleteBtn {
   display: inline-block;
   width: 1.5rem;
   height: 1.5rem;
   background-color: #838586;
   text-align: center;
   border-radius: 50%;
   margin-right: 0.5rem;
   color: #ced4da;
   font-weight: bold;
   font-size: 0.8rem;
   line-height: 1.5rem;
   cursor: pointer;
}
.btnContainer {
   display: flex;
   justify-content: center;
   margin: 2rem;
}

@media screen and (max-width: 576px) {
   .emailListContainer {
      width: 100%;
      font-size: 80%;
      margin-right: 0rem;
   }
   .pageContainer {
      padding-right: 0 !important;
      padding-left: 0 !important;
      margin: 1rem !important;
      .formContainer {
         padding: 1rem !important;
         margin-right: 0rem !important;
         .section {
            padding-right: 0 !important;
         }
      }
   }
   .deleteBtn {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      background-color: #838586;
      text-align: center;
      border-radius: 50%;
      margin-right: 0.5rem;
      color: #ced4da;
      font-weight: bold;
      font-size: 0.7rem;
      line-height: 1rem;
      cursor: pointer;
   }
}
